import React from 'react';
import { Link } from 'react-router-dom';
// import Cabecera from './cabecera';
// import Menu from './menu';

const Opciones = () => {
  return (
    <aside className="main-sidebar sidebar-dark-primary elevation-4">

      <Link to="/menu/" className="brand-link">
        <img src="http://18.234.135.140:8001/static/dist/img/AdminLTELogo.png" alt="AdminLTE Logo" className="brand-image img-circle elevation-3" style={{ opacity: 0.8 }} />
        <span className="brand-text font-weight-light">ComedorApp</span>
      </Link>
    
      <div className="sidebar">
        
        <nav className="mt-2">
          <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
            
            <li className="nav-header">OPCIONES</li>
            <li className="nav-item">
              <Link to="/menu/" className="nav-link">
                <i className="nav-icon far fa-calendar-alt"></i>
                <p>
                  Inicio
                  <span className="badge badge-info right">2</span>
                </p>
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/turnos/" className="nav-link">
                <i className="nav-icon far fa-image"></i>
                <p>
                  Turnos
                </p>
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/mi-turno/" className="nav-link">
                <i className="nav-icon fas fa-columns"></i>
                <p>
                  Mi turno
                </p>
              </Link>
            </li>
            
          </ul>
        </nav>
        
      </div>
    
    </aside>
  );
};

export default Opciones;
