import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const SesionSi = () => {
  const navigate = useNavigate();
  
  useEffect(() => {
    const session = localStorage.getItem('session');
    if (session === '1') {
      navigate('/turnos'); // Redirecciona a la pgina deseada (por ejemplo, '/turnos')
    }
  }, [navigate]);
  
  return null; // No renderiza nada, solo redirige en funcin de la sesin
};

export default SesionSi;