import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import jwt from "jsonwebtoken";
// import SesionSi from './sesionSi'; // Importa el componente
import SesionNo from "./sesionNo"; // Importa el componente
const Login = () => {
  const navigate = useNavigate();

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [showModal, setShowModal] = useState(false); // Estado para mostrar el modal de error
  const [campoRequerido, setCampoRequerido] = useState(false);

  // const handleLogin = () => {
  const handleLogin = (e) => {
    e.preventDefault(); // Evitar el comportamiento predeterminado del formulario
    setError("");
    setShowModal(false); // Ocultar el modal antes de hacer la solicitud

    if (!username.trim() || !password.trim()) {
      // setShowModal(true); // Mostrar el modal si al menos uno de los campos est vaco
      setCampoRequerido(true);
      return;
    }
    // axios.post('api/login/', {
    // axios.post('http://127.0.0.1:8000/api/login/', {
    axios.post("http://18.234.135.140:8001//api/login/", {
        usuario: username,
        clave: password,
      })

      .then((response) => {
        if (response.data.error) {
          setError(response.data.error);
          setShowModal(true); // Mostrar el modal solo en caso de error
        } else {
          localStorage.setItem("token", response.data.token);
          const token = jwt.decode(response.data.token);
          const reservaValue = token.reserva ? "1" : "0";
          const session = token.session ? "1" : "0";
          localStorage.setItem("reserva_usuario", reservaValue);
          localStorage.setItem("session", session);
          navigate("/turnos/");
        }
      })
      .catch((error) => {
        setError("Hubo un error en el servidor");
        setShowModal(true); // Mostrar el modal solo en caso de error
        console.error("Error de inicio de sesin:", error);
      });
  };

  return (
    <div className="hold-transition login-page">
      {/* <SesionSi /> */}
      <SesionNo />

      <div
        className={`modal fade ${showModal ? "show" : ""}`}
        id="modal-sm"
        style={{ display: showModal ? "block" : "none" }}
      >
        <div className="modal-dialog modal-sm">
          <div className="modal-content">
            <div className="modal-header">
              <h4 className="modal-title">Error</h4>
              <button
                type="button"
                className="close"
                onClick={() => setShowModal(false)}
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>

            <div className="modal-body">
              <p>{error}</p>
            </div>

            <div className="modal-footer justify-content-between">
              <button
                type="button"
                className="btn btn-default"
                data-dismiss="modal"
                onClick={() => setShowModal(false)}
              >
                Cerrar
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="login-box">
        <div className="card card-outline card-primary">
          <div className="card-header text-center">
            <a href="/login" className="h1">
              <b>Comedor</b>App
            </a>
          </div>

          <div className="card-body">
            <p className="login-box-msg">Iniciar sesin</p>
            <form onSubmit={handleLogin}>
              <div
                className={`input-group mb-3 ${campoRequerido &&
                  !username &&
                  "has-error"}`}
              >
                <input
                  type="text"
                  className={`form-control ${
                    campoRequerido && !username ? "is-invalid" : ""
                  }`}
                  placeholder="Usuario"
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                />
                <div className="input-group-append">
                  <div className="input-group-text">
                    <span className="fas fa-user"></span>
                  </div>
                </div>
                {/* {campoRequerido && !username && <span className="error-message">* Campo requerido.</span>} */}
              </div>

              <div
                className={`input-group mb-3 ${campoRequerido &&
                  !password &&
                  "has-error"}`}
              >
                <input
                  type="password"
                  className={`form-control ${
                    campoRequerido && !password ? "is-invalid" : ""
                  }`}
                  placeholder="Clave"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
                <div className="input-group-append">
                  <div className="input-group-text">
                    <span className="fas fa-lock"></span>
                  </div>
                </div>
                {/* {campoRequerido && !password && <span className="error-message">* Campo requerido.</span>} */}
              </div>

              <div className="input-group mb-3">
                {/* <button onClick={handleLogin} class="btn btn-primary btn-block" data-toggle="modal" data-target="#modal-sm">Ingresar</button> */}
                {/* <button onClick={handleLogin} className="btn btn-primary btn-block">Ingresar</button> */}
                <button type="submit" className="btn btn-primary btn-block">
                  Ingresar
                </button>
              </div>
            </form>
            <p className="mb-1">
              <a href="/recuperar-clave/">Olvide mi clave</a>
            </p>
            <p className="mb-0">
              <a href="/registrar-usuario/" className="text-center">
                Registrar usuario
              </a>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
