// import React, { useState } from 'react';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom'; // Importar Link
import jwt from 'jsonwebtoken';

const TerminarSesion = () => {
  const navigate = useNavigate();
  // const [showConfirmDialog, setShowConfirmDialog] = useState(false);

  const token = localStorage.getItem('token');
  const decodedToken = jwt.decode(token);

  // const handleCerrarSesion = () => {
  //   setShowConfirmDialog(true);
  // };

  const handleCerrarSesion = () => {
    localStorage.removeItem('session');
    localStorage.removeItem('token');
    localStorage.removeItem('reserva_usuario');
    // localStorage.removeItem('usuario');
    navigate('/login');
  }

  // const handleConfirmCerrarSesion = () => {
  //   localStorage.removeItem('session');
  //   localStorage.removeItem('token');
  //   localStorage.removeItem('reserva_usuario');
  //   navigate('/login');
  // };

  // const handleCancelCerrarSesion = () => {
  //   setShowConfirmDialog(false);
  // };

  const handleConfiguraciones = () => {
    navigate('/configuraciones');
  };

  return (
    <ul className="navbar-nav ml-auto">
      <li className="nav-item dropdown user-menu">
        <Link to="#" className="nav-link dropdown-toggle" data-toggle="dropdown">
          <img
            src="http://18.234.135.140:8001/static/dist/img/user2-160x160.jpg"
            className="user-image img-circle elevation-2"
            alt=""
          />
          {decodedToken && (<span className="d-none d-md-inline">{decodedToken.nombre}</span>)}
        </Link>
        <ul className="dropdown-menu dropdown-menu-lg dropdown-menu-right">
          <li className="user-header bg-primary">
            <img
              src="http://18.234.135.140:8001/static/dist/img/user2-160x160.jpg"
              className="img-circle elevation-2"
              alt=""
            />
            {decodedToken && (<p>{decodedToken.nombre}</p>)}
            {/* <p>
              {decodedToken.nombre} 
              - {decodedToken.rol}
              <small>Miembro desde {decodedToken.fechaIngreso}</small>
            </p> */}
          </li>
          <li className="user-footer">
            <Link to="#" className="btn btn-default btn-flat" onClick={handleConfiguraciones}>Perfil</Link>
            <button className="btn btn-default btn-flat float-right" onClick={handleCerrarSesion}>Cerrar sesin</button>
          </li>
        </ul>
      </li>
      <li className="nav-item">
        <Link to="#" className="nav-link" data-widget="fullscreen">
          <i className="fas fa-expand-arrows-alt"></i>
        </Link>
      </li>
      <li className="nav-item">
        <Link
          to="#"
          className="nav-link"
          data-widget="control-sidebar"
          data-slide="true"
        >
          <i className="fas fa-th-large"></i>
        </Link>
      </li>
      
      {/* {showConfirmDialog && (
        <div className="confirm-dialog">
          <p>Est seguro que desea cerrar la sesin?</p>
          <button onClick={handleConfirmCerrarSesion}>Aceptar</button>
          <button onClick={handleCancelCerrarSesion}>Cancelar</button>
        </div>
      )} */}


    </ul>
  );
};

export default TerminarSesion;
