import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const RegistrarUsuario = () => {
    const navigate = useNavigate();

    const [usuario, setUsuario] = useState('');
    const [clave, setClave] = useState('');
    const [repetirClave, setRepetirClave] = useState('');
    const [nombre, setNombre] = useState('');
    const [apellidoP, setApellidoP] = useState('');
    const [apellidoM, setApellidoM] = useState('');
    const [fechaNacimiento, setFechaNacimiento] = useState('');
    const [correo, setCorreo] = useState('');
    const [telefono, setTelefono] = useState('');
    const [mensaje, setMensaje] = useState('');
    const [mensajeCampo, setMensajeCampo] = useState('');

    const requiredFields = ['usuario', 'clave', 'repetirClave', 'nombre', 'apellidoP', 'apellidoM', 'fechaNacimiento', 'correo', 'telefono'];

    const [showModal, setShowModal] = useState(false); // Estado para mostrar el modal de error
    const [modalMensaje, setModalMensaje] = useState('');

    

    const handleSubmit = (e) => {
        e.preventDefault();

        setShowModal(false); // Ocultar el modal antes de hacer la solicitud
        setModalMensaje('');
        const phoneNumberPattern = /^[0-9]+$/; // Expresin regular para validar dgitos

        const data = {
            usuario,
            clave,
            repetirClave,
            nombre,
            apellidoP,
            apellidoM,
            fechaNacimiento,
            correo,
            telefono,
        };
        // console.log(data);
        const missingFields = requiredFields.filter(field => !data[field]);

        if (missingFields.length) {
            setMensajeCampo('Falta llenar los campos: ' + missingFields.join(', '));
            return;
        }

        if (clave !== repetirClave) {
            // setMensaje('Las claves no coinciden. Por favor, verifique.');
            setModalMensaje('Las claves no coinciden. Por favor, verifique.');
            setShowModal(true);
            return;
        }

        if (!phoneNumberPattern.test(telefono)) {
            setModalMensaje('Error al ingresar el nro de telefono.');
            setShowModal(true);
            return;
        }

        const userData = {
            usuario: usuario,
            clave: clave,
            repetir_clave: repetirClave,
            nombre: nombre,
            ape_p: apellidoP,
            ape_m: apellidoM,
            fec_nac: fechaNacimiento,
            correo: correo,
            telefono: telefono,
        };

        // axios.post('api/registrar-usuario/', userData)
        // axios.post('http://127.0.0.1:8000/api/registrar-usuario/', userData)
        axios.post('http://18.234.135.140:8001/api/registrar-usuario/', userData)
            .then(response => {
                if (response.data.error) {
                    // setMensaje(response.data.error);
                    setModalMensaje(response.data.error);
                    setShowModal(true);
                } else {
                    setMensaje(response.data.mensaje);
                    setTimeout(() => {
                        navigate('/login');
                    }, 1000);
                }
            })
            .catch(error => {
                // setMensaje('Error al registrar el usuario');
                setModalMensaje('Error al registrar el usuario');
                console.error('Error al registrar el usuario:', error);
                setShowModal(true);
            });
    };

    return (
        <div className="hold-transition register-page2">
            <div className="register-box">

            <div className={`modal fade ${showModal ? 'show' : ''}`} id="modal-sm" style={{ display: showModal ? 'block' : 'none' }}>
                <div className="modal-dialog modal-sm">

                <div className="modal-content">

                    <div className="modal-header">
                    <h4 className="modal-title">Error</h4>
                    <button type="button" className="close" onClick={() => setShowModal(false)}>
                        <span aria-hidden="true">&times;</span>
                    </button>
                    </div>
                    
                    <div className="modal-body">
                    {/* <p>{mensaje}</p> */}
                    <p>{modalMensaje}</p>
                    </div>
                    
                    <div className="modal-footer justify-content-between">
                    <button type="button" className="btn btn-default" data-dismiss="modal" onClick={() => setShowModal(false)}>Cerrar</button>
                    </div>

                </div>

                </div>
            </div>


                <div className="card card-outline card-success">
                    <div className="card-header text-center">
                        <a href="/login/" className="h1">
                            <b>Comedor</b>App
                        </a>
                    </div>
                    <div className="card-body">
                        <p className="login-box-msg">Registrar nuevo usuario</p>
                        <form onSubmit={handleSubmit}>
                            <div className="input-group mb-3">
                                <input type="text" className={`form-control ${mensajeCampo && !usuario ? 'is-invalid' : ''}`} placeholder="Usuario" value={usuario} onChange={(e) => setUsuario(e.target.value)} />
                                <div class="input-group-append">
                                    <div class="input-group-text">
                                    <span class="fas fa-user"></span>
                                    </div>
                                </div>    
                                {/* {mensajeCampo && !usuario && <span className="error-message">* Campo requerido</span>} */}
                            </div>
                            <div className="input-group mb-3">
                                <input type="password" className={`form-control ${mensajeCampo && !clave ? 'is-invalid' : ''}`} placeholder="Clave" value={clave} onChange={(e) => setClave(e.target.value)} />
                                <div class="input-group-append">
                                    <div class="input-group-text">
                                    <span class="fas fa-lock"></span>
                                    </div>
                                </div>
                                {/* {mensajeCampo && !clave && <span className="error-message">* Campo requerido</span>} */}
                            </div>
                            <div className="input-group mb-3">
                                <input type="password" className={`form-control ${mensajeCampo && !repetirClave ? 'is-invalid' : ''}`} placeholder="Repetir Clave" value={repetirClave} onChange={(e) => setRepetirClave(e.target.value)} />
                                <div class="input-group-append">
                                    <div class="input-group-text">
                                    <span class="fas fa-lock"></span>
                                    </div>
                                </div>
                                {/* {mensajeCampo && !repetirClave && <span className="error-message">* Campo requerido</span>} */}
                            </div>
                            <div className="input-group mb-3">
                                <input type="text" className={`form-control ${mensajeCampo && !nombre ? 'is-invalid' : ''}`} placeholder="Nombre" value={nombre} onChange={(e) => setNombre(e.target.value)} />
                                <div class="input-group-append">
                                    <div class="input-group-text">
                                    <span class="fas fa-user"></span>
                                    </div>
                                </div>
                                {/* {mensajeCampo && !nombre && <span className="error-message">* Campo requerido</span>} */}
                            </div>
                            <div className="input-group mb-3">
                                <input type="text" className={`form-control ${mensajeCampo && !apellidoP ? 'is-invalid' : ''}`} placeholder="Apellido Paterno" value={apellidoP} onChange={(e) => setApellidoP(e.target.value)} />
                                <div class="input-group-append">
                                    <div class="input-group-text">
                                    <span class="fas fa-user"></span>
                                    </div>
                                </div>
                                {/* {mensajeCampo && !apellidoP && <span className="error-message">* Campo requerido</span>} */}
                            </div>
                            <div className="input-group mb-3">
                                <input type="text" className={`form-control ${mensajeCampo && !apellidoM ? 'is-invalid' : ''}`} placeholder="Apellido Materno" value={apellidoM} onChange={(e) => setApellidoM(e.target.value)} />
                                <div class="input-group-append">
                                    <div class="input-group-text">
                                    <span class="fas fa-user"></span>
                                    </div>
                                </div>
                                {/* {mensajeCampo && !apellidoM && <span className="error-message">* Campo requerido</span>} */}
                            </div>
                            <label>Fecha de nacimiento:</label>
                            <div className="input-group mb-3" id="reservationdate" data-target-input="nearest">
                                <input type="date" className={`form-control ${mensajeCampo && !fechaNacimiento ? 'is-invalid' : ''}`} placeholder="Fecha de Nacimiento (AAAA-MM-DD)" value={fechaNacimiento} onChange={(e) => setFechaNacimiento(e.target.value)} />
                                {/* {mensajeCampo && !fechaNacimiento && <span className="error-message">* Campo requerido</span>} */}
                            </div>
                            <div className="input-group mb-3">
                                <input type="email" className={`form-control ${mensajeCampo && !correo ? 'is-invalid' : ''}`} placeholder="Correo" value={correo} onChange={(e) => setCorreo(e.target.value)} />
                                <div class="input-group-append">
                                    <div class="input-group-text">
                                    <span class="fas fa-envelope"></span>
                                    </div>
                                </div>
                                {/* {mensajeCampo && !correo && <span className="error-message">* Campo requerido</span>} */}
                            </div>
                            <div className="input-group mb-3">
                                <div class="input-group-prepend">
                                    <span class="input-group-text"><i class="fas fa-phone"></i></span>
                                </div>
                                <input type="tel" className={`form-control ${mensajeCampo && !telefono ? 'is-invalid' : ''}`} placeholder="Telefono" value={telefono} onChange={(e) => setTelefono(e.target.value)} />
                                {/* {mensajeCampo && !telefono && <span className="error-message">* Campo requerido</span>} */}
                            </div>
                            <div className="input-group mb-3">
                                <button type="submit" className="btn btn-primary btn-block">Registrar</button>
                            </div>
                            <a href="/login/" className="text-center">Ya tengo una cuenta</a>
                        </form>
                        {mensaje && <p>{mensaje}</p>}
                        
                    </div>
                </div>
            </div>
        </div>    
    );
};

export default RegistrarUsuario;

