import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import jwt from 'jsonwebtoken';
import { useNavigate } from 'react-router-dom';

import SesionNo from './sesionNo';

// import TerminarSesion from './terminarSesion';
import Opciones from './opciones';
import Cabecera from './cabecera';
import Pie from './pie';
// Configurar token CSRF para Axios
axios.defaults.xsrfHeaderName = "X-CSRFToken";
axios.defaults.xsrfCookieName = "csrftoken";

const getCookie = (name) => {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) return parts.pop().split(';').shift();
};

const MiTurno = () => {
  const [reserva, setReserva] = useState(null);
  const [mensaje, setMensaje] = useState('');
  const [mostrarConfirmacion, setMostrarConfirmacion] = useState(false);

  const [mensajeRespuesta, setMensajeRespuesta] = useState(null); // Estado para almacenar la respuesta del servidor
  const [liberarReservaSeleccionado, setLiberarReservaSeleccionado] = useState(null);
  const token = localStorage.getItem('token');
  const decodedToken = jwt.decode(token);
  
  const [cerrar, setCerrar] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    // Obtener el token desde localStorage
    // const token = localStorage.getItem('token');
    if (token) {
      try {
        // const decodedToken = jwt.decode(token);
        const id_usuario = decodedToken.usuario_id;

        axios
          // .get(`api/mostrar-solicitud/${id_usuario}/`)
          // .get(`http://127.0.0.1:8000/api/mostrar-solicitud/${id_usuario}/`)
          .get(`http://18.234.135.140:8001/api/mostrar-solicitud/${id_usuario}/`)
          .then(response => {
            setReserva(response.data);
            // console.log('response:',response.data)
            if (response.data.error) {
              setMensajeRespuesta(response.data.error);
            } else {
              setMensajeRespuesta(response.data.mensaje);
              // ...
            }
          })
          .catch(error => console.error('Error fetching data:', error));
      } catch (error) {
        console.error('Error al decodificar el token:', error);
      }
    }
  }, []);

  const handleLiberarReserva = (reserva) => {
    // console.log('reserva:',reserva)
    if (reserva) {
      const id_turno = Number(reserva.id_turno);
      if (!Number.isInteger(id_turno)) {
        setMensaje('El id_turno debe ser un nmero.');
        return;
      }
  
      // Mostrar el mensaje de confirmacin
      setMensaje(`Ests seguro de liberar la reserva del turno ${reserva.id_turno}?`);
      setMostrarConfirmacion(true);
      setLiberarReservaSeleccionado(reserva); // Cambia esto para pasar el id_turno
    }
  };

  // const cancelarLiberarReserva = () => {
  //   // Ocultar el mensaje de confirmacin y limpiar el mensaje
  //   setMostrarConfirmacion(false);
  //   setMensaje('');
  // };

  const liberarReserva = (liberarReservaSeleccionado) => {
    // console.log("liberarReservaSeleccionado:", liberarReservaSeleccionado); // Agrega esta lnea
    if (token && liberarReservaSeleccionado) {
      try {
        const id_usuario = decodedToken.usuario_id;
        const id_turno = liberarReservaSeleccionado.id_turno; // Utiliza directamente el valor del id_turno
  
        // Obtener el token CSRF desde las cookies
        const csrfToken = getCookie('csrftoken'); // Implementa esta funcin para obtener la cookie CSRF
  
        axios
          .post(
            // 'api/liberar-reserva/',
            // 'http://127.0.0.1:8000/api/liberar-reserva/',
            'http://18.234.135.140:8001/api/liberar-reserva/',
            { id_usuario, id_turno },
            {
              headers: {
                'X-CSRFToken': csrfToken, // Agregar el token CSRF en el encabezado
              },
            }
          )
          .then(response => {
            if (response.data.error) {
              setMensajeRespuesta(response.data.error);
              
            } else {
              setMensajeRespuesta(response.data.mensaje);
              setCerrar(response.data.cerrar);  
              localStorage.setItem('reserva_usuario', '0');
              // ...
            }
          })
          .catch(error => {
            console.error('Error al liberar la reserva:', error);
          })
          .finally(() => {
            // Ocultar el mensaje de confirmacin despus de liberar la reserva
            setMostrarConfirmacion(false);
          });
      } catch (error) {
        console.error('Error al decodificar el token:', error);
      }
    }
  };
  const handleCerrarModal = () => {
    if (cerrar){
      navigate('/menu/');
    }
  };
  


  return (
    <div>
      <Cabecera/>
      <Opciones/>
      <div className="content-wrapper">
      
        <section className="content-header">
          <div className="container-fluid">
            <div className="row mb-2">
              <div className="col-sm-6">
                <h1>Detalle de la Reserva</h1>
              </div>
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item"><Link to="/menu/">Inicio</Link></li>
                  <li className="breadcrumb-item active">Reserva</li>
                </ol>
              </div>
            </div>
          </div>
        </section>

        <section className="content">

          <div className="card">
            <div className="card-header">
              <h3 className="card-title">Mi turno</h3>

              <div className="card-tools">
                <button type="button" className="btn btn-tool" data-card-widget="collapse" title="Collapse">
                  <i className="fas fa-minus"></i>
                </button>
                <button type="button" className="btn btn-tool" data-card-widget="remove" title="Remove">
                  <i className="fas fa-times"></i>
                </button>
              </div>
            </div>
              <div className="card-body">

                    <SesionNo />   
                    {reserva ? (
                      <div>
                        <p>ID de Turno: {reserva.id_turno}</p>
                        <p>ID de Usuario: {reserva.id_usuario}</p>
                        <p>Nombre: {reserva.nombre}</p>
                        <p>Apellido Paterno: {reserva.ape_p}</p>
                        <p>Hora de Inicio: {reserva.hora_ini}</p>
                        <p>Hora de Fin: {reserva.hora_fin}</p>
                        <p>Accin: {reserva.accion}</p>
                        {/* <button onClick={() => handleLiberarReserva(reserva)}>Liberar Reserva</button> */}
                        <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#modal-default" onClick={() => handleLiberarReserva(reserva)}>liberar reserva</button>
                      </div>
                    ) : (
                      <p>No se encontraron datos de reserva.</p>
                    )}
                    {/* {mensajeRespuesta && (
                      <div>
                        <p>{mensajeRespuesta}</p>
                      </div>
                    )} */}

                    {/* {mostrarConfirmacion && (
                      <div>
                        <p>{mensaje}</p>
                        <button type="button" className="btn btn-outline-primary btn-sm" title="" data-toggle="tooltip" data-original-title="btn btn-outline-primary" onClick={() => liberarReserva(liberarReservaSeleccionado)}>Aceptar</button>
                        <button type="button" className="btn btn-outline-primary btn-sm" title="" data-toggle="tooltip" data-original-title="btn btn-outline-primary" onClick={cancelarLiberarReserva}>Cancelar</button>
                      </div>
                    )} */}

                  {/* <button type="button" className="btn btn-default" data-toggle="modal" data-target="#modal-default">
                    Launch Default Modal
                  </button> */}

                  <div className="modal fade" id="modal-default">
                    <div className="modal-dialog">
                      <div className="modal-content">
                        <div className="modal-header">
                          <h4 className="modal-title">Confirmacin</h4>
                          <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                          </button>
                        </div>
                        <div className="modal-body">
                          {mostrarConfirmacion ? (
                            <div>
                              <p>{mensaje}</p>
                            </div>
                          ) : (
                            <div>
                              {mensajeRespuesta && (
                                <div>
                                  <p>{mensajeRespuesta}</p>
                                </div>
                              )}
                            </div>
                          )}
                        </div>
                        <div className="modal-footer justify-content-between">
                          <button type="button" className="btn btn-default" data-dismiss="modal" onClick={handleCerrarModal}>
                            {mostrarConfirmacion ? 'Cancelar' : 'Cerrar'}
                          </button>
                          {mostrarConfirmacion && (
                            <button
                              type="button"
                              className="btn btn-primary"
                              onClick={() => {
                                liberarReserva(liberarReservaSeleccionado);
                              }}
                            >
                              Aceptar
                            </button>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>   
              </div>
            
            <div className="card-footer">
              Footer
            </div>
          </div>
        </section>
      </div>
      <Pie/>
    </div>
  );
};

export default MiTurno;


