import React from 'react';
import { Link } from 'react-router-dom';
import Opciones from './opciones';
import Cabecera from './cabecera';
import Pie from './pie';
import SesionNo from './sesionNo';

const Menu = () => {
  return (
    <div>
      <Cabecera />
      <Opciones />
      <SesionNo />
      <div className="content-wrapper">
      
        <section className="content-header">
          <div className="container-fluid">
            <div className="row mb-2">
              <div className="col-sm-6">
                <h1>Bienvenido!</h1>
              </div>
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item"><Link to="/menu/">Inicio</Link></li>
                  <li className="breadcrumb-item active">Men</li>
                </ol>
              </div>
            </div>
          </div>
        </section>

        <section className="content">

          <div className="card">
            <div className="card-header">
              <h3 className="card-title">Men</h3>

              <div className="card-tools">
                <button type="button" className="btn btn-tool" data-card-widget="collapse" title="Collapse">
                  <i className="fas fa-minus"></i>
                </button>
                <button type="button" className="btn btn-tool" data-card-widget="remove" title="Remove">
                  <i className="fas fa-times"></i>
                </button>
              </div>
            </div>
            <div className="card-body">
              Check the Header part you can find Legacy version of style.
              <br />
              Start creating your amazing application!
            </div>
            
            <div className="card-footer">
              Footer
            </div>
          </div>
        </section>
      </div>
      <Pie/>
    </div>
    
  );
};

export default Menu;
