import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const RecuperarClaveNo = () => {
  const navigate = useNavigate();
  
  useEffect(() => {
    const usuario = localStorage.getItem('usuario');
    if (!usuario) {
      navigate('/recuperar-clave'); // Redirecciona a la pgina deseada (por ejemplo, '/turnos')
    }
  }, [navigate]);
  
  return null; // No renderiza nada, solo redirige en funcin de la sesin
};

export default RecuperarClaveNo;