import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import RecuperarClaveNo from "./recuperarClaveNo";
import axios from "axios";
import Swal from "sweetalert2";

const NuevaClave = () => {
  const navigate = useNavigate();

  const [nuevaClave, setNuevaClave] = useState("");
  const [repetirNuevaClave, setRepetirNuevaClave] = useState("");
  const [mensaje, setMensaje] = useState("");

  const usuario = localStorage.getItem("usuario");
  // const [usuario, setUsuario]= useState('');
  const [campoRequerido, setCampoRequerido] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    // setUsuario('qwerty');
    if (!usuario.trim() || !nuevaClave.trim() || !repetirNuevaClave.trim()) {
      // setShowModal(true); // Mostrar el modal si al menos uno de los campos est vaco
      setCampoRequerido(true);
      return;
    }

    if (nuevaClave !== repetirNuevaClave) {
      // setMensaje('Las contraseas no coinciden. Por favor, verifique.');
      Swal.fire({
        icon: "warning",
        title: "Las contraseas no coinciden. Por favor, verifique.",
      });
      return;
    }

    try {
      // const response = await axios.post('api/cambiar-clave/', {
      // const response = await axios.post('http://127.0.0.1:8000/api/cambiar-clave/', {  
      const response = await axios.post("http://18.234.135.140:8001/api/cambiar-clave/",{
          usuario: usuario,
          clave: nuevaClave,
          repetir_clave: repetirNuevaClave,
        }
      );

      if (response.data.error) {
        // setMensaje(response.data.error);
        Swal.fire({
          icon: "warning",
          title: response.data.error,
        });
      } else {
        setMensaje(response.data.mensaje);
        // Swal.fire({
        //     icon: 'warning',
        //     title: response.data.mensaje,
        // });
        localStorage.removeItem("usuario");
        setTimeout(() => {
          navigate("/login");
        }, 1000);
      }
    } catch (error) {
      // setMensaje('Error al procesar la solicitud');
      Swal.fire({
        icon: "warning",
        title: "Error al procesar la solicitud",
      });
      console.error("Error al procesar la solicitud:", error);
    }
  };

  return (
    <div className="register-box">
      <RecuperarClaveNo />
      <div className="card card-outline card-primary">
        <div className="card-header text-center">
          <a href="/login/" className="h1">
            <b>Comedor</b>App
          </a>
        </div>
        <div className="card-body">
          <p className="login-box-msg">Cambiar contrasea</p>
          <form onSubmit={handleSubmit}>
            <div className="input-group mb-3">
              <input
                type="text"
                className={`form-control ${
                  campoRequerido && !usuario ? "is-invalid" : ""
                }`}
                placeholder="Usuario"
                value={usuario}
                disabled
              />
              <div className="input-group-append">
                <div className="input-group-text">
                  <span className="fas fa-user"></span>
                </div>
              </div>
            </div>
            <div className="input-group mb-3">
              <input
                type="password"
                className={`form-control ${
                  campoRequerido && !nuevaClave ? "is-invalid" : ""
                }`}
                placeholder="Nueva Contrasea"
                value={nuevaClave}
                onChange={(e) => setNuevaClave(e.target.value)}
              />
              <div className="input-group-append">
                <div className="input-group-text">
                  <span className="fas fa-lock"></span>
                </div>
              </div>
            </div>
            <div className="input-group mb-3">
              <input
                type="password"
                className={`form-control ${
                  campoRequerido && !repetirNuevaClave ? "is-invalid" : ""
                }`}
                placeholder="Repetir Nueva Contrasea"
                value={repetirNuevaClave}
                onChange={(e) => setRepetirNuevaClave(e.target.value)}
              />
              <div className="input-group-append">
                <div className="input-group-text">
                  <span className="fas fa-lock"></span>
                </div>
              </div>
            </div>
            <div className="input-group mb-3">
              <button type="submit" className="btn btn-primary btn-block">
                Cambiar Contrasea
              </button>
            </div>
          </form>
          <a href="/login/" className="text-center">
            Login
          </a>
        </div>
      </div>
      {mensaje && <p>{mensaje}</p>}
    </div>
  );
};

export default NuevaClave;
