import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom'; // Importa Link
import jwt from 'jsonwebtoken';

import { useNavigate } from 'react-router-dom';
// import SesionNo from './sesionNo'; // Importa el componente
// import TerminarSesion from './terminarSesion';
// import { useParams } from 'react-router-dom'; // Importa el hook useParams
import Opciones from './opciones';
import Cabecera from './cabecera';
import Pie from './pie';
import SesionSi from './sesionSi';
// import SesionNo from './sesionNo';
// import Login from './componentes/login';



// const decodedToken = jwt.decode(token);
// console.log('token:', decodedToken);

// console.log('reserva_usuario:', localStorage.getItem('reserva_usuario'));
// console.log('session:', localStorage.getItem('session'));

const Turnos = () => {
  const [turnos, setTurnos] = useState([]);
  const [mensajeRespuesta, setMensajeRespuesta] = useState(null); // Estado para almacenar la respuesta del servidor
  
  const [mostrarConfirmacion, setMostrarConfirmacion] = useState(false);
  const [turnoSeleccionado, setTurnoSeleccionado] = useState(null);
  // const { token } = useParams();
  const [cerrar, setCerrar] = useState('');
  const navigate = useNavigate();
  
  const token = localStorage.getItem('token');// esto lo tengo que colocar aqui porque ponerlo hace que no cargue el componente ni decodifique la data de la variable "token"
  const decodedToken = jwt.decode(token); // Decodificar el token aqu para acceder al nombre del usuario

  useEffect(() => {
    // Utiliza useParams() para obtener el token desde los parmetros de la ruta
    

    if (token) {
      try {

        // const decodedToken = jwt.decode(token);
        
        // Puedes utilizar los datos del usuario si es necesario
        // console.log('Datos del usuario:', decodedToken);

        // axios.get('api/turnos/')
        // axios.get('http://127.0.0.1:8000/api/turnos/')
        axios.get('http://18.234.135.140:8001/api/turnos/')  
        .then(response => setTurnos(response.data))
          .catch(error => console.error('Error fetching data:', error));
      } catch (error) {
        console.error('Error al decodificar el token:', error);
      }
    }
  }, [token]);

  const handleSeleccionar = (turno) => {
    setMostrarConfirmacion(true);
    setTurnoSeleccionado(turno);
  };

  const handleReservar = (turno) => {
    // const token = props.location.state.token;

    if (token) {
      try {
        // const decodedToken = jwt.decode(token);
        // Puedes utilizar los datos del usuario si es necesario
        
        // console.log('Datos del usuario:', decodedToken);

        // Enviar solicitud POST para procesar la seleccin del turno
        // axios.post('procesar-solicitud/', {
        // axios.post('http://127.0.0.1:8000/procesar-solicitud/', {
        axios.post('http://18.234.135.140:8001/procesar-solicitud/', {
          id_turno: turno.id_turno,
          id_usuario: decodedToken.usuario_id,
          nombre: decodedToken.nombre,
          ape_p: decodedToken.ape_p,
          hora_ini: turno.hora_ini,
          hora_fin: turno.hora_fin,
          accion: 1
        })
          .then(response => {
            if(response.data.error){
              setMensajeRespuesta(response.data.error);

            }else{
              setMensajeRespuesta(response.data.mensaje);
              setCerrar(response.data.cerrar); 
              localStorage.setItem('reserva_usuario', '1');
              // console.log('reserva_usuario:', localStorage.getItem('reserva_usuario'));
              // ...
            }
          })
          .catch(error => {
            if (error.response && error.response.data && error.response.data.error) {
              // Si el servidor enva un mensaje de error, lo capturamos y lo establecemos en el estado
              // console.error('Error al decodificar el token:', error.response.data.error);
              setMensajeRespuesta(error.response.data.error);
            } else {
              // En caso de otro error, mostramos un mensaje genrico
              setMensajeRespuesta('Ha ocurrido un error al procesar la solicitud.');
            }
            // ...
          });
      } catch (error) {
        console.error('Error al decodificar el token:', error);
      }
    }
    setMostrarConfirmacion(false);
  };
  
  const handleCerrarModal = () => {
    if (cerrar){
      navigate('/mi-turno/');
    }
  };

  // const handleCancelar = () => {
  //   setMostrarConfirmacion(false);
  // };
  // const handleReservarLibre = (turno) => {
  //   // Lgica para reservar el turno
  //   console.log('Turno reservado:', turno);
  // };

  return (
    <div>
      <Cabecera/>
      <Opciones/>
      <SesionSi/>
      {/* <SesionNo /> */}
      <div className="content-wrapper">
      
        <section className="content-header">
          <div className="container-fluid">
            <div className="row mb-2">
              <div className="col-sm-6">
                <h1>Detalle de turnos</h1>
              </div>
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item"><Link to="/menu/">Inicio</Link></li>
                  <li className="breadcrumb-item active">Turnos</li>
                </ol>
              </div>
            </div>
          </div>
        </section>

        <section className="content">

          <div className="card">
            <div className="card-header">
              <h3 className="card-title">Turnos</h3>

              <div className="card-tools">
                <button type="button" className="btn btn-tool" data-card-widget="collapse" title="Collapse">
                  <i className="fas fa-minus"></i>
                </button>
                <button type="button" className="btn btn-tool" data-card-widget="remove" title="Remove">
                  <i className="fas fa-times"></i>
                </button>
              </div>
            </div>
            <div className="card-body">
            <table className="table table-bordered table-hover">
              <thead>
                <tr>
                  {/* <th>id_turno</th> */}
                  <th>Turno</th>
                  {/* <th>Inicio</th>
                  <th>Fin</th> */}
                  <th>Cantidad</th>
                  <th>Estado</th>
                  <th>Accin{/* Columna para los botones */}</th>
                </tr>
              </thead>
              <tbody>
                {turnos.map(turno => (
                  <tr key={turno.id_turno}>
                    {/* <td>{turno.id_turno}</td> */}
                    <td>{turno.turno}</td>
                    {/* <td>{turno.hora_ini}</td>
                    <td>{turno.hora_fin}</td> */}
                    <td>{turno.cant_turno}/{turno.cant_turno_total}</td>

                    <td className="project_progress">
                      <div className="progress progress-sm">
                        <div className={`progress-bar ${((turno.cant_turno / turno.cant_turno_total) * 100).toFixed(0) >= 100 ? 'bg-red' : 'bg-default'}`} role="progressbar" aria-valuenow={((turno.cant_turno / turno.cant_turno_total) * 100).toFixed(0)} aria-valuemin="0" aria-valuemax="100" style={{ width: `${((turno.cant_turno / turno.cant_turno_total) * 100).toFixed(0)}%` }}>
                        </div>
                      </div>
                      <small>
                        {((turno.cant_turno / turno.cant_turno_total) * 100).toFixed(0)}% Completado
                      </small>
                    </td>
                    
                    <td> 
                      
                      
                      {turno.id_turno === 11 ? (
                          localStorage.getItem('reserva_usuario') === '0' ? (
                            <button className="btn btn-primary" data-toggle="modal" data-target="#modal-default" onClick={() => handleSeleccionar(turno)}>Libre</button> 
                          ) : (
                            <div>No disp.</div>  
                          )   
                        ) : (
                          turno.cant_turno === turno.cant_turno_total ? (
                              //Los dems turnos estn llenos
                              <div>No disp.</div>
                            ) : (
                                localStorage.getItem('reserva_usuario') === '0' ? (
                                  <button className="btn btn-primary" data-toggle="modal" data-target="#modal-default" onClick={() => handleSeleccionar(turno)}>Libre</button>
                                ):(
                                  <div>No disp.</div>  
                                )
                            )  
                        )}
                        
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>

              {/* {mensajeRespuesta && (
                <div>
                  <p>{mensajeRespuesta}</p>
                </div>
              )}
              {mostrarConfirmacion && turnoSeleccionado && (
                <div>
                  <p>Est seguro que quiere reservar el turno {turnoSeleccionado.turno}?</p>
                  <button onClick={() => handleReservar(turnoSeleccionado)}>Aceptar</button>
                  <button onClick={handleCancelar}>Cancelar</button>
                </div>
              )} */}
                
              <div className="modal fade" id="modal-default">
                <div className="modal-dialog">
                  <div className="modal-content">
                    <div className="modal-header">
                      <h4 className="modal-title">Confirmacin de Reserva</h4>
                      <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                      </button>
                    </div>
                    <div className="modal-body">
                      {mensajeRespuesta && (
                        <div>
                          <p>{mensajeRespuesta}</p>
                        </div>
                      )}
                      {mostrarConfirmacion && turnoSeleccionado && (
                        <div>
                          <p>
                            Est seguro que quiere reservar el turno: {turnoSeleccionado.id_turno === 11 ? turnoSeleccionado.turno : turnoSeleccionado.id_turno}?
                          </p>
                        </div>
                      )}
                    </div>
                    <div className="modal-footer justify-content-between">
                      <button type="button" className="btn btn-default" data-dismiss="modal" onClick={handleCerrarModal}>
                        {mostrarConfirmacion ? 'Cancelar' : 'Cerrar'}
                      </button>
                      {mostrarConfirmacion && (
                        <button
                          type="button"
                          className="btn btn-primary"
                          onClick={() => {
                            handleReservar(turnoSeleccionado);
                            setMensajeRespuesta('Turno reservado exitosamente!');
                            setMostrarConfirmacion(false); // Cerrar el modal despus de la confirmacin
                          }}
                         
                        >
                          Aceptar
                        </button>
                      )}
                    </div>
                  </div>
                </div>
              </div>




            </div>
            
            <div className="card-footer">
              Footer
            </div>
          </div>
        </section>
      </div>
      <Pie/>
    </div>
  );
};

export default Turnos;

    
