import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";

const RecuperarClave = () => {
  const [usuario, setUsuario] = useState("");
  const [contacto, setContacto] = useState("telefono"); // Valor por defecto es "telefono"
  const [telefono, setTelefono] = useState("");
  const [correo, setCorreo] = useState("");
  const [mensaje, setMensaje] = useState("");
  const [codigo, setCodigo] = useState("");
  const [idUsuario, setIdUsuario] = useState("");
  const [mostrarIngresarCodigo, setMostrarIngresarCodigo] = useState(false); // Estado para mostrar el botn
  const navigate = useNavigate();
  const [campoRequerido, setCampoRequerido] = useState(false);

  const handleSubmit = async (e) => {
    // console.log('usuario:',localStorage.getItem('usuario'));
    e.preventDefault();

    // if (!usuario.trim() || !correo.trim() || !telefono.trim()) {
    //     // setShowModal(true); // Mostrar el modal si al menos uno de los campos est vaco
    //     setCampoRequerido(true);
    //     return;
    // }

    // Validar si el campo de usuario est vaco y se ha elegido "correo" o "telfono"
    if (
      !usuario.trim() ||
      (!telefono.trim() && contacto === "telefono") ||
      (!correo.trim() && contacto === "correo")
    ) {
      setCampoRequerido(true);
      return;
    }

    // console.log('data:',usuario,contacto,telefono,correo);

    try {
      // const response = await axios.post('api/recuperar-clave/', {
      // const response = await axios.post('http://127.0.0.1:8000/api/recuperar-clave/', {
      const response = await axios.post("http://18.234.135.140:8001/api/recuperar-clave/",{
          usuario,
          contacto, // Enva la opcin seleccionada (telefono o correo)
          telefono,
          correo,
        }
      );

      if (response.data.error) {
        // setMensaje(response.data.error);
        setMostrarIngresarCodigo(false); // Ocultar el botn en caso de error
        Swal.fire({
          icon: "warning",
          title: response.data.error,
        });
      } else {
        setMensaje(response.data.mensaje);
        console.log(response.data);
        if (response.data.id_usuario) {
          setIdUsuario(response.data.id_usuario);
          // console.log('id_usuario:',response.data.id_usuario);
          setMostrarIngresarCodigo(true); // Mostrar el botn en caso de xito
        }
        // Swal.fire({
        //     icon: 'warning',
        //     title: response.data.mensaje,
        // });
      }
    } catch (error) {
      setMensaje("Error al procesar la solicitud");
      console.error("Error al procesar la solicitud:", error);
    }
  };
  // console.log('mensaje:', mensaje);
  // const handleButtonClick = () => {
  //     Swal.fire({
  //       icon: 'warning',
  //       title: mensaje
  //     });
  //   };

  const handleIngresarCodigo = async () => {
    try {
      // Enva el cdigo y el id_usuario al servidor para validar
      // const response = await axios.post('api/validar-codigo/', {
      // const response = await axios.post('http://127.0.0.1:8000/api/validar-codigo/', {
      const response = await axios.post("http://18.234.135.140:8001/api/validar-codigo/",{
          id_usuario: idUsuario, // Cambia esto por el valor correcto
          codigo: codigo, // Cambia esto por el cdigo ingresado por el usuario
        }
      );

      if (response.data.error) {
        setMensaje(response.data.error);
      } else {
        setMensaje(response.data.mensaje);
        localStorage.setItem("usuario", response.data.usuario);
        setTimeout(() => {
          navigate("/nueva-clave/");
        }, 1000);
      }
    } catch (error) {
      setMensaje("Error al validar el cdigo");
      console.error("Error al validar el cdigo:", error);
    }
  };

  return (
    <div className="hold-transition register-page">
      <div className="register-box">
        <div className="card card-outline card-warning">
          <div className="card-header text-center">
            <a href="/login/" className="h1">
              <b>Comedor</b>App
            </a>
          </div>
          <div className="card-body">
            <p className="login-box-msg">Recuperar contrasea</p>
            <form onSubmit={handleSubmit}>
              <div className="input-group mb-3">
                <input
                  type="text"
                  className={`form-control ${
                    campoRequerido && !usuario ? "is-invalid" : ""
                  }`}
                  placeholder="Usuario"
                  value={usuario}
                  onChange={(e) => setUsuario(e.target.value)}
                />

                <div className="input-group-append">
                  <div className="input-group-text">
                    <span className="fas fa-user"></span>
                  </div>
                </div>
              </div>

              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <button
                    type="button"
                    className="btn btn-default dropdown-toggle"
                    data-toggle="dropdown"
                  >
                    Accion
                  </button>
                  <div className="dropdown-menu">
                    <button
                      className="dropdown-item"
                      type="button"
                      onClick={() => setContacto("telefono")}
                    >
                      Telefono
                    </button>
                    <button
                      className="dropdown-item"
                      type="button"
                      onClick={() => setContacto("correo")}
                    >
                      Correo
                    </button>
                  </div>
                </div>

                {contacto === "telefono" ? (
                  <input
                    type="tel"
                    className={`form-control ${
                      campoRequerido && !telefono ? "is-invalid" : ""
                    }`}
                    placeholder="Telfono"
                    value={telefono}
                    onChange={(e) => setTelefono(e.target.value)}
                  />
                ) : (
                  <input
                    type="email"
                    className={`form-control ${
                      campoRequerido && !correo ? "is-invalid" : ""
                    }`}
                    placeholder="Correo"
                    value={correo}
                    onChange={(e) => setCorreo(e.target.value)}
                  />
                )}
                {contacto === "telefono" ? (
                  <div className="input-group-prepend">
                    {/* <span class="input-group-text"><i class="fas fa-phone"></i></span> */}
                    <div className="input-group-text">
                      <span className="fas fa-phone"></span>
                    </div>
                  </div>
                ) : (
                  <div className="input-group-append">
                    <div className="input-group-text">
                      <span className="fas fa-envelope"></span>
                    </div>
                  </div>
                )}
                {/* <div class="input-group-append">
                                    <div class="input-group-text">
                                    <span class="fas fa-envelope"></span>
                                    </div>
                                </div>  */}
              </div>

              {/* <div className="input-group">
                                <div>elija forma de recuperacin</div>
                                <select class="btn btn-default dropdown-toggle" value={contacto} onChange={(e) => setContacto(e.target.value)}>
                                    
                                        <option value="telefono">Telfono</option>
                                        <option value="correo">Correo</option>
                                    
                                </select>
                            </div>

                            {contacto === 'telefono' ? (
                                <div className="input-group">
                                    <input type="tel" placeholder="Telfono" value={telefono} onChange={(e) => setTelefono(e.target.value)} />
                                </div>
                            ) : (
                                <div className="input-group">
                                    <input type="email" placeholder="Correo" value={correo} onChange={(e) => setCorreo(e.target.value)} />
                                </div>
                            )} */}
              <button type="submit" className="btn btn-primary btn-block">
                Recuperar Contrasea
              </button>
              {/* <button onClick={handleButtonClick}>Mostrar Notificacin</button> */}
            </form>
            {mensaje && (
              <div>
                <p>{mensaje}</p>
                {mostrarIngresarCodigo && (
                  <div className="input-group mb-3">
                    <input
                      type="text"
                      className="form-control rounded-0"
                      placeholder="Ingrese el cdigo"
                      value={codigo}
                      onChange={(e) => setCodigo(e.target.value)}
                    />
                    <div className="input-group-prepend">
                      <span className="input-group-append">
                        <button
                          onClick={handleIngresarCodigo}
                          className="btn btn-primary btn-block btn-flat"
                        >
                          Validar Cdigo
                        </button>
                      </span>
                    </div>
                  </div>
                )}
              </div>
            )}

            <p className="mt-3 mb-1">
              <a href="/login/">Login</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RecuperarClave;
